  --dark-0: rgb(16, 18, 19);
  --dark-1: rgb(56, 69, 82);
  --dark-1a: rgba(56, 69, 82, 0.8);
  --light-0: rgb(216, 227, 230);
  --light-1: rgb(185, 205, 211);
  --light-2: rgb(132, 154, 160);
  --light-3: rgb(92, 112, 117);

  --check: rgb(91, 188, 217);
  --priority-low: rgb(113, 193, 74);
  --priority-med: rgb(248, 161, 80);
  --priority-high: rgb(232, 86, 86);
}

body {
  padding: 40px;
  font-family: 'Roboto', system-ui, sans-serif;
  background-color: var(--light-1);
  color: var(--light-0);
}

h1 {
  color: var(--dark-1);
  font-weight: 800;
  font-size: 2rem;
  line-height: 1;
  padding-bottom: 40px;
}

#content {
  display: flex;
  gap: 20px;
  align-items: flex-start;
}

.project,
#new-project-btn {
  background-color: var(--dark-0);
  padding: 15px;
  width: 350px;
  min-width: 350px;
  border-radius: 15px;
}
.project {
  position: relative;
  padding-bottom: 5px;
  border-top-right-radius: 0;

  h2 {
    padding-right: 15px; /* compensate for menu icon */
    padding-bottom: 15px;
    .progress {
      color: var(--light-1);
      margin-left: 15px;
      font-size: 1rem;
      font-weight: 500;
      overflow-wrap: normal; /* don't wrap text */
    }
  }
  .no-todos {
    font-size: 0.85rem;
    color: var(--light-2);
  }

  .new-project-todo-btn {
    width: 100%;
    margin-top: 15px;
    font-size: 30px;
    color: var(--light-2);
  }
}

#new-project-btn {
  font-size: 1.3rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  /* plus icon */
  iconify-icon {
    color: var(--light-2);
    font-size: 30px;
    /* prevent text shifting before icon loads in */
    width: 30px;
  }
}

.todo {
  position: relative;
  background-color: var(--dark-1);
  padding: 10px 15px;
  border-radius: 5px 0 5px 5px; /* round corners except top right */
  /* display checkbox in column left of todo info */
  display: grid;
  grid-template-columns: 20px auto;
  gap: 15px;

  + .todo {
    margin-top: 10px;
  }

  /* checkbox border */
  .pretty .state label:before {
    border-color: var(--light-2);
  }
  .pretty .state label:before,
  .pretty .state label:after {
    border-width: 2px;
  }
  /* checkmark */
  .pretty input:checked ~ .state .svg {
    color: var(--dark-0);
  }
  /* checkbox bg */
  .pretty input:checked ~ .state label:after,
  .pretty.p-toggle .state label:after {
    background-color: var(--check);
  }
  /* fix checkbox position */
  .pretty .state label:before,
  .pretty .state label:after,
  .pretty.p-svg .state .svg {
    top: 20px;
  }

  .todo-info {
    h3 {
      word-break: break-all;
      padding-right: 10px; /* compensate for menu icon */
      font-weight: 500;
      font-size: 1rem;
    }
    /* align due date and priority on same line */
    .todo-dueDate,
    .todo-priority {
      display: inline-block;
      line-height: 1;
    }
    .todo-dueDate {
      font-size: 0.85rem;
      /* gap before priority */
      &:not(:empty) {
        margin-right: 15px;
      }
    }
    .todo-priority {
      font-size: 0.8rem;
      border: 2px solid;
      border-radius: 50px;
      padding: 2px 10px;
      &.priority-Low {
        border-color: var(--priority-low);
      }
      &.priority-Medium {
        border-color: var(--priority-med);
      }
      &.priority-High {
        border-color: var(--priority-high);
      }
    }
    .todo-desc {
      color: var(--light-1);
      font-size: 0.75rem;
      line-height: 1.3;
      word-break: break-word;
      &:not(:empty) {
        margin-top: 5px;
      }
    }
  }

  /* fade out complete todos */
  &.complete {
    background-color: var(--dark-1a);
    color: var(--light-2);
    h3 {
      text-decoration: line-through;
    }
  }
}

#new-todo-btn {
  z-index: 1000;
  position: absolute;
  bottom: 40px;
  right: 40px;
  font-size: 40px;
  color: var(--light-2);
  background-color: var(--dark-0);
  border-radius: 40px 40px 0 40px; /* round corners except bottom right */
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
