___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --backdrop-0: rgba(169, 190, 195,, 0);
  --backdrop-1: rgba(169, 190, 195, 0.5);
}

#dialog {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--dark-0);
  padding: 20px 25px;
  min-width: 500px;
  border-radius: 15px 0 15px 15px; /* round corners except top right */

  #close-btn {
    position: absolute;
    top: 20px;
    right: 25px;
    font-size: 30px;
    color: var(--light-3);
  }

  #form-fields {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
  }
  
  form {
    h2 {
      text-align: center;
      font-size: 1.3rem;
      margin-bottom: 10px;
    }

    label {
      color: var(--light-1);
      font-size: 0.85rem;
      display: block;
      margin-bottom: 5px;
    }
    .form-input {
      width: 100%;
    }
    input,
    select,
    textarea {
      background-color: var(--dark-1);
      padding: 5px 10px;
      border-radius: 5px;
    }
    input,
    select {
      height: 45px;
    }

    /* span both columns */
    li:has(#name-input),
    li:has(#date-input),
    li:has(#desc-input) {
      grid-column: 1 / span 2;
    }

    /* date picker calendar icon */
    #date-input::-webkit-calendar-picker-indicator {
      color-scheme: dark;
    }

    li:has(#desc-input) {
      margin-top: 10px;
    }
    #desc-input {
      font-size: 0.85rem;
      resize: vertical;
      height: 100px;
      max-height: 200px;
      overflow-y: auto;
    }

    .dialog-btns {
      margin-top: 30px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;

      button {
        padding: 6px;
        width: 150px;
        border-radius: 5px;
      }
    }
    #submit-btn {
      background-color: var(--dark-1a);
      color: var(--light-0);
      font-weight: 500;
      justify-self: right;
    }
    #cancel-btn {
      border: 2px solid var(--dark-1);
      color: var(--light-1);
      justify-self: left;
    }
  }
}

/* open state */
dialog[open] {
  opacity: 1;
}
/* closed state */
dialog {
  opacity: 0;
  transition:
    opacity 50ms ease-out,
    overlay 50ms ease-out allow-discrete,
    display 50ms ease-out allow-discrete;
}

@starting-style {
  dialog[open] {
    opacity: 0;
  }
}
dialog::backdrop {
  background-color: var(--backdrop-0);
  transition:
    overlay 50ms allow-discrete,
    display 50ms allow-discrete,
    background-color 50ms;
}
dialog[open]::backdrop {
  background-color: var(--backdrop-1);
}
@starting-style {
  dialog[open]::backdrop {
    background-color: var(--backdrop-0);
  }
}`, "",{"version":3,"sources":["webpack://./src/css/dialog.css"],"names":[],"mappings":"AAAA;EACE,qCAAqC;EACrC,sCAAsC;AACxC;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,QAAQ;EACR,gCAAgC;EAChC,+BAA+B;EAC/B,kBAAkB;EAClB,gBAAgB;EAChB,+BAA+B,EAAE,mCAAmC;;EAEpE;IACE,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,eAAe;IACf,qBAAqB;EACvB;;EAEA;IACE,aAAa;IACb,8BAA8B;IAC9B,SAAS;EACX;;EAEA;IACE;MACE,kBAAkB;MAClB,iBAAiB;MACjB,mBAAmB;IACrB;;IAEA;MACE,qBAAqB;MACrB,kBAAkB;MAClB,cAAc;MACd,kBAAkB;IACpB;IACA;MACE,WAAW;IACb;IACA;;;MAGE,+BAA+B;MAC/B,iBAAiB;MACjB,kBAAkB;IACpB;IACA;;MAEE,YAAY;IACd;;IAEA,sBAAsB;IACtB;;;MAGE,uBAAuB;IACzB;;IAEA,8BAA8B;IAC9B;MACE,kBAAkB;IACpB;;IAEA;MACE,gBAAgB;IAClB;IACA;MACE,kBAAkB;MAClB,gBAAgB;MAChB,aAAa;MACb,iBAAiB;MACjB,gBAAgB;IAClB;;IAEA;MACE,gBAAgB;MAChB,aAAa;MACb,8BAA8B;MAC9B,SAAS;;MAET;QACE,YAAY;QACZ,YAAY;QACZ,kBAAkB;MACpB;IACF;IACA;MACE,gCAAgC;MAChC,qBAAqB;MACrB,gBAAgB;MAChB,mBAAmB;IACrB;IACA;MACE,+BAA+B;MAC/B,qBAAqB;MACrB,kBAAkB;IACpB;EACF;AACF;;AAEA,eAAe;AACf;EACE,UAAU;AACZ;AACA,iBAAiB;AACjB;EACE,UAAU;EACV;;;wCAGsC;AACxC;;AAEA;EACE;IACE,UAAU;EACZ;AACF;AACA;EACE,mCAAmC;EACnC;;;yBAGuB;AACzB;AACA;EACE,mCAAmC;AACrC;AACA;EACE;IACE,mCAAmC;EACrC;AACF","sourcesContent":[":root {\n  --backdrop-0: rgba(169, 190, 195,, 0);\n  --backdrop-1: rgba(169, 190, 195, 0.5);\n}\n\n#dialog {\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n  background-color: var(--dark-0);\n  padding: 20px 25px;\n  min-width: 500px;\n  border-radius: 15px 0 15px 15px; /* round corners except top right */\n\n  #close-btn {\n    position: absolute;\n    top: 20px;\n    right: 25px;\n    font-size: 30px;\n    color: var(--light-3);\n  }\n\n  #form-fields {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    gap: 15px;\n  }\n  \n  form {\n    h2 {\n      text-align: center;\n      font-size: 1.3rem;\n      margin-bottom: 10px;\n    }\n\n    label {\n      color: var(--light-1);\n      font-size: 0.85rem;\n      display: block;\n      margin-bottom: 5px;\n    }\n   